var self;
	export default {
		onLoad() {
			self=this;
		},
		data() {
			return {
				hotcity:["桂林","柳州","崇左","玉林","呼和浩特","深圳","钦州","广州","上海",
				],
				current_city:"南宁市",
				current_district:"",
				current_street:"",
			}
		},
		methods: {
			relocation:function(){
				uni.removeStorage({
				    key: 'city',
				    success: function () {}
				});
				uni.removeStorage({
				    key: 'district',
				    success: function () {}
				});
				uni.removeStorage({
				    key: 'street',
				    success: function () {}
				});
				uni.getLocation({
					type:"gcj02",
					geocode:true,
					success: function (res) {
						// console.log('国家：' + res.address.country);
						//console.log('省份：' + res.address.province);
						// console.log('城市：' + res.address.city);
						// console.log('区/县：' + res.address.district);
						// console.log('街道：' + res.address.street);
						uni.setStorage({
							key: 'city',
							data: res.address.city
						});
						uni.getStorage({
							key: 'city',
							success: function (res) {
									self.current_city= res.data;
								}
						});
						uni.setStorage({
							key: 'district',
							data: res.address.district
						});
						uni.getStorage({
							key: 'district',
							success: function (res) {
									self.current_district= res.data;
								}
						});
						uni.setStorage({
							key: 'street',
							data: res.address.street
						});
						uni.getStorage({
							key: 'street',
							success: function (res) {
									self.current_street= res.data;
								}
						});
						uni.showToast({
						    title: '重新定位完成'
						});
					    }
				});
			},
			toTest:()=>{
				uni.navigateTo({
					url: 'test',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
